import './assets/css/index.less';
import * as TWEEN from 'tween';
import Global from './components/global';
var gl, timeout;
const main = function () {
  //console.log(TWEEN);
  gl = new Global({
    el: 'id-test',
    isMove: true
  });
  if(!gl.animate) {
    gl.animate = () => {
      TWEEN.update();
      gl.render();
    }
    gl.load();
  }
  document.getElementById('id0').onclick = function() {
    new TWEEN.Tween(gl).to({interpolatePos: 0.0}, 800).onUpdate(function() {
      gl.geometryNeedsUpdate = true;
    }).onComplete(function() {
      gl.geometryNeedsUpdate = false;
      gl.isMode = 'id0';
    }).easing(TWEEN.Easing.Cubic.Out).start();
  }
  document.getElementById('id1').onclick = function() {
    new TWEEN.Tween(gl).to({interpolatePos: 1.0}, 800).onUpdate(function() {
      gl.geometryNeedsUpdate = true;
    }).onComplete(function() {
      gl.geometryNeedsUpdate = false;
      gl.isMode = 'id1';
    }).easing(TWEEN.Easing.Cubic.Out).start();
  }
  document.getElementById('id1').onclick();
  gl.isMode = 'id1';
  // gl.flight([{
  //   start: {
  //     lat: -27,
  //     lon: 43
  //   },
  //   end: {
  //     lat: 53,
  //     lon: -98
  //   }
  // }, {
  //   start: {
  //     lat: -13,
  //     lon: -92
  //   },
  //   end: {
  //     lat: 53,
  //     lon: -98
  //   }
  // }]);
  handle();
}
const handle = function() {
  clearTimeout(timeout);
  if(gl.isMove) {
    if(gl.isMode == 'id1') {
      document.getElementById('id0').onclick();
    } else if(gl.isMode == 'id0') {
      document.getElementById('id1').onclick();
    }
  }
  timeout = setTimeout(handle, 3000);
}
main();
