import * as THREE from 'three';
//import './TrackballControls';
import TrackballControls from 'three-trackballcontrols';
import Geo from './geo';
export default class Gobal {
  constructor(props) {
    this.Geo = Geo;
    this.radius = 100;
    this.interpolatePos = 0.0;
    this.countries = [];
    Object.assign(this, props);
    this.wrap = document.getElementById(this.el);
    this.el = document.createElement("div");
    this.el.style = "user-select: none;width: 100%;height: 100%;";
    this.wrap.appendChild(this.el);
    this.containt = document.createElement("div");
    this.containt.style = "user-select: none;";
    this.wrap.appendChild(this.containt);
    if(!this.point) {
      this.point = new THREE.Mesh(new THREE.CubeGeometry(0.5, 0.5, 1, 1, 1, 1));
    }
    this.renderer = new THREE.WebGLRenderer({
      preserveDrawingBuffer: this.mode == 1 ? true : false,
      antialias: false,
      alpha: true
    });
    var w = this.el.offsetWidth || window.innerWidth,
    h = this.el.offsetHeight || window.innerHeight;
    this.renderer.setSize(w, h);
    this.renderer.autoClear = false;
    this.renderer.sortObjects = false;    
    this.renderer.generateMipmaps = false;          
    this.el.appendChild(this.renderer.domElement);
    this.scene = new THREE.Scene();
    this.scene.matrixAutoUpdate = false;
    this.camera = new THREE.PerspectiveCamera(45, w / h, 1, 2000);
    this.camera.position.x = 0;
    this.camera.position.y = 0;
    this.camera.position.z = 300;
    this.scene.add(this.camera);
    this.rotating = new THREE.Object3D();
    //this.camera.position.x = 80;
    this.scene.add(this.rotating);
    this.visualizationMesh = new THREE.Object3D();
    this.rotating.add(this.visualizationMesh);
    this.createLight();
    
    // var geometry = new THREE.SphereGeometry(this.radius, 32, 32);
    // //geometry.applyMatrix( new THREE.Matrix4().makeScale(1.0, 1.0, 1.5));
    // //For ellipsoid testing: geometry.applyMatrix( new THREE.Matrix4().makeScale( 1.0, 1.0, 1.5 ) ); Try 6378137.0, 6356752.314140
    // var material = new THREE.MeshPhongMaterial({
    //   wireframe: true,
    //   transparent: true
    // });
    // var sphere = new THREE.Mesh(geometry, material);
    // this.scene.add(sphere);
    
    //this.test();
    
    //this.createGlobal();
    //this.createBox();
    
    this.createMap();
    
    this.controls = new TrackballControls(this.camera, this.renderer.domElement, this);
    // this.controls.rotateSpeed = 0.5;
    // this.controls.zoomSpeed = 1.0;
    // this.controls.panSpeed = 0.25;
    // this.controls.noRotate = false;
    // this.controls.noZoom = false;
    // this.controls.noPan = true;
    // this.controls.staticMoving = false;
    // this.controls.dynamicDampingFactor = 0.2;
  }
  flight(data) {
    if (this.fls !== undefined && this.fls.length) {
      for(var i = 0; i < this.fls.length; i++) {
        this.rotating.remove(this.fls[i]);
        var o, ol, zobject;
        if (this.fls[i] instanceof THREE.Mesh || this.fls[i] instanceof THREE.ParticleSystem) {
          for (o = this.scene.__webglObjects.length - 1; o >= 0; o--) {
            zobject = this.scene.__webglObjects[o].object;
            if (this.fls[i] == zobject) {
              this.scene.__webglObjects.splice(o, 1);
            }
          }
        }
        this.fls[i] = null;
      }
    }
    this.fls = [];
    if(!data || !data.length) {
      return;
    }
    for(var i = 0; i < data.length; i++) {
      var s_lat = parseInt(data[i].start.lat, 10);
      var s_lon = parseInt(data[i].start.lon, 10);
      var e_lat = parseInt(data[i].end.lat, 10);
      var e_lon = parseInt(data[i].end.lon, 10);

      var sv = {
        lat: s_lat,
        lon: s_lon,
        name: data[i].start.name,
        value: data[i].start.value || 0,
        center: new THREE.Vector3(
          -this.radius * Math.sin((90 - s_lat) * Math.PI / 180) * Math.cos((s_lon - 180) * Math.PI / 180), 
          this.radius * Math.cos((90 - s_lat) * Math.PI / 180), 
          this.radius * Math.sin((90 - s_lat) * Math.PI / 180) * Math.sin((s_lon - 180) * Math.PI / 180)
        )
      };

      var ev = {
        lat: e_lat,
        lon: e_lon,
        name: data[i].end.name,
        value: data[i].end.value || 0,
        center: new THREE.Vector3(
          -this.radius * Math.sin((90 - e_lat) * Math.PI / 180) * Math.cos((e_lon - 180) * Math.PI / 180), 
          this.radius * Math.cos((90 - e_lat) * Math.PI / 180), 
          this.radius * Math.sin((90 - e_lat) * Math.PI / 180) * Math.sin((e_lon - 180) * Math.PI / 180)
        )
      }
      var attributes = {
        pos: {
          type: 'f',
          value: []
        },
        max: {
          type: 'f',
          value: []
        }
      }, 
      number, 
      linesGeo = new THREE.Geometry(), 
      start_lat = sv.lat, 
      start_lon = sv.lon, 
      end_lat = ev.lat, 
      end_lon = ev.lon, 
      max_height = data[i].style != undefined && data[i].style.height != undefined ? data[i].style.height : this.flightHeight, 
      points = [], 
      G = Math.random().toFixed(2) * 300 + 300;
      //var points = [];
      number = G;
      for (var j = 0; j < G; j++) {
        var arc_angle = j * 180.0 / G;
        var arc_radius = this.radius + (Math.sin(arc_angle * Math.PI / 180.0)) * max_height;
        var s_lat = start_lat * Math.PI / 180.0;
        var s_lon = start_lon * Math.PI / 180.0;
        var e_lat = end_lat * Math.PI / 180.0;
        var e_lon = end_lon * Math.PI / 180.0;
        var d = 2 * Math.asin(Math.sqrt(Math.pow((Math.sin((s_lat - e_lat) / 2)), 2) + Math.cos(s_lat) * Math.cos(e_lat) * Math.pow(Math.sin((s_lon - e_lon) / 2), 2)));
        var A = Math.sin((1 - j / G) * d) / Math.sin(d);
        var B = Math.sin(j / G * d) / Math.sin(d);
        var x = A * Math.cos(s_lat) * Math.cos(s_lon) + B * Math.cos(e_lat) * Math.cos(e_lon);
        var y = A * Math.cos(s_lat) * Math.sin(s_lon) + B * Math.cos(e_lat) * Math.sin(e_lon);
        var z = A * Math.sin(s_lat) + B * Math.sin(e_lat);
        var lat = Math.atan2(z, Math.sqrt(Math.pow(x, 2) + Math.pow(y, 2))) * 180 / Math.PI;
        var lon = Math.atan2(y, x) * 180 / Math.PI;
        linesGeo.vertices.push(new THREE.Vector3(
          -arc_radius * Math.sin((90 - lat) * Math.PI / 180) * Math.cos((lon - 180) * Math.PI / 180), 
          arc_radius * Math.cos((90 - lat) * Math.PI / 180), 
          arc_radius * Math.sin((90 - lat) * Math.PI / 180) * Math.sin((lon - 180) * Math.PI / 180)
        ));
        attributes.pos.value.push(j);
        attributes.max.value.push(G);
      }
      var uniforms = {
        size: {
          type: "f",
          value: parseFloat(data[i].style != undefined && data[i].style.weight != undefined ?  data[i].style.weight : this.flightWeight, 10)
        },
        c: {
          type: "f",
          value: 1.0
        },
        p: {
          type: "f",
          value: 1.4
        },
        glowColor: {
          type: "c",
          value: new THREE.Color(0xffffff)
        },
        viewVector: {
          type: "v3",
          value: this.camera.position
        },
        time: {
          type: "f",
          value: 0.0
        },
        leng: {
          type: "f",
          value: 80.0
        },
        color: {
          type: "c",
          value: new THREE.Color(data[i].style != undefined && data[i].style.color != undefined ? data[i].style.color : this.flightColor)
        },
        pColor: {
          type: "c",
          value: new THREE.Color(0xffffff)
        },
        /*sColor: {
          type: "c",
          value: new THREE.Color(0xC91224)
        },*/
        texture: {
          type: "t",
          value: 0,
          texture: THREE.ImageUtils.loadTexture('images/earth/particle.png')
        }
      };
      var shaderMaterial = new THREE.ShaderMaterial({
        attributes: attributes,
        uniforms: uniforms,
        vertexShader: `
          uniform vec3 viewVector;
          uniform float c;
          uniform float p;
          uniform float size;
          varying float intensity;
          attribute float pos;
          attribute float max;
          varying float vMax;
          varying float vPos;
          varying vec3 vNormal;
          uniform float leng;
          uniform float time;
          void main() {
            vPos = pos;
            vMax = max;
            if(vPos >= time && vPos <= time + leng) {
              //if(vPos >= time + leng - 30.0 && vPos <= time + leng) {
                gl_PointSize = size;
              //} else {
                //gl_PointSize = 4.0;
              //}
            } else {
              gl_PointSize = 4.0;
            }
            if(vPos == 15.0 || vPos == vMax - 15.0) {
              gl_PointSize = 30.0;
            }
            vec4 mvPosition = modelViewMatrix * vec4(position, 1.0);
            gl_Position = projectionMatrix * mvPosition;
          }
        `,
        fragmentShader: `
          uniform float time;
          uniform float leng;
          uniform vec3 color;
          varying float vPos;
          varying float vMax;
          varying vec3 vNormal;
          uniform sampler2D texture;
          uniform vec3 pColor;
          uniform vec3 glowColor;
          //uniform vec3 sColor;
          varying float intensity;
          void main() {
            if(vPos >= time && vPos <= time + leng) {
              //if(vPos >= time + leng - 30.0 && vPos <= time + leng) {
                gl_FragColor = vec4(color * glowColor, 1.0);
                gl_FragColor = gl_FragColor * texture2D(texture, gl_PointCoord);
              //}
            } else {
              gl_FragColor = vec4(color * glowColor, 0.22);
              gl_FragColor = gl_FragColor * texture2D(texture, gl_PointCoord);
            }
            if(vPos == 15.0 || vPos == vMax - 15.0) {
              gl_FragColor = vec4(color * glowColor, 1.0);
              gl_FragColor = gl_FragColor * texture2D(texture, gl_PointCoord);
            }
          }
        `,
        blending: THREE.AdditiveBlending,
        depthTest: true,
        depthWrite: false,
        transparent: true
      });
      var points = new THREE.ParticleSystem(linesGeo, shaderMaterial);
      this.rotating.add(points);
      this.fls.push(points);
      var self = this;
      var speed = data[i].style != undefined && data[i].style.speed != undefined ? data[i].style.speed : this.flightSpeed;
      points.update = function() {
        if(this.material.uniforms.time.value >= this.geometry.vertices.length) {
          this.material.uniforms.time.value = 0;
        } else {
          this.material.uniforms.time.value += parseInt(speed, 10);
        }
        this.geometry.verticesNeedUpdate = true;
      }
    }
  }
  createMap() {
    var base = new THREE.Geometry();
    var count = 0;
    for(var i = 0; i < this.Geo.features.length; i++) {
      var gm = this.Geo.features[i].geometry;
      var coordinates = gm.coordinates;
      if(gm.type == 'Polygon') {
        var path = [];
        for(var a = 0; a < coordinates.length; a++) {
          for(var b = 0; b < coordinates[a].length; b++) {
            var v = this.createVector3('plane', coordinates[a][b][0], coordinates[a][b][1]);
            path.push(v);
            count++;
          }
        }
        var shape = new THREE.Shape(path);
        var geometry = new THREE.ShapeGeometry(shape);
        //this.countries[i].geometry = geometry;
        //base.merge(geometry);
        //colors.push(new THREE.MeshBasicMaterial({color: 0x6b7e00}));
        //var mbt = i % 2 == 0 ? new THREE.MeshBasicMaterial({color: 0xb3c400}) : new THREE.MeshBasicMaterial({color: 0x6b7e00});
        //var mesh = new THREE.Mesh(geometry, mbt);
        //this.scene.add(mesh);
        this.countries.push({
          properties: this.Geo.features[i].properties,
          geometry: geometry,
          geometry2D: geometry.clone(),
          geometry3D: geometry.clone(),
          color: 0x33C9FB
        });
        // var mesh = new THREE.Line(geometry, new THREE.MeshBasicMaterial({color: 0xb3c400}));
        // this.scene.add(mesh);
      } else if(gm.type == 'MultiPolygon') {
        for(var a = 0; a < coordinates.length; a++) {
          var path = [];
          for(var b = 0; b < coordinates[a].length; b++) {
            for(var c = 0; c < coordinates[a][b].length; c++) {
              var v = this.createVector3('plane', coordinates[a][b][c][0], coordinates[a][b][c][1]);
              path.push(v);
              count++;
            }
          }
          var shape = new THREE.Shape(path);
          var geometry = new THREE.ShapeGeometry(shape);
          //this.countries[i].geometry = geometry;
          //base.merge(geometry);
          //colors.push(new THREE.MeshBasicMaterial({color: 0xb3c400}));
          //var mbt = i % 2 == 0 ? new THREE.MeshBasicMaterial({color: 0xb3c400}) : new THREE.MeshBasicMaterial({color: 0x6b7e00});
          //var mesh = new THREE.Mesh(geometry, mbt);
          //this.scene.add(mesh);
          this.countries.push({
            properties: this.Geo.features[i].properties,
            geometry: geometry,
            geometry2D: geometry.clone(),
            geometry3D: geometry.clone(),
            color: 0x237DB9
          });
          // var mesh = new THREE.Line(geometry, new THREE.MeshBasicMaterial({color: 0xb3c400}));
          // this.scene.add(mesh);
        }
      }
    }
    //var mesh = new THREE.Mesh(base, new THREE.MeshBasicMaterial({color: 0xb3c400}));
    //this.scene.add(mesh);
    //return false;

    for(var i = 0; i < this.countries.length; i++) {
      for(var k = 0; k < this.countries[i].geometry.vertices.length; k++) {
        var lon = this.countries[i].geometry.vertices[k].x * 90 / this.radius;
        var lat = this.countries[i].geometry.vertices[k].y * 90 / this.radius;
        this.countries[i].geometry3D.vertices[k].x = -this.radius * Math.sin((90 - lat) * Math.PI / 180) * Math.cos((lon - 180) * Math.PI / 180);
        this.countries[i].geometry3D.vertices[k].y = this.radius * Math.cos((90 - lat) * Math.PI / 180);
        this.countries[i].geometry3D.vertices[k].z = this.radius * Math.sin((90 - lat) * Math.PI / 180) * Math.sin((lon - 180) * Math.PI / 180);
      }
    }
    this.bufferGeometry = new THREE.BufferGeometry();
    var positions = new Float32Array(count * 3 * 3);
    var normals = new Float32Array(count * 3 * 3);
    var colors = new Float32Array(count * 3 * 3);
    var color = new THREE.Color(0x237DB9);
    var index = 0;
    for(var i = 0; i < this.countries.length; i++) {
      var vertices = this.countries[i].geometry.vertices;
      for(var f = 0; f < this.countries[i].geometry.faces.length; f++) {
        var face = this.countries[i].geometry.faces[f];

        positions[index ] = vertices[face.a].x;
        positions[index + 1] = vertices[face.a].y;
        positions[index + 2] = vertices[face.a].z;

        positions[index + 3] = vertices[face.b].x;
        positions[index + 4] = vertices[face.b].y;
        positions[index + 5] = vertices[face.b].z;

        positions[index + 6] = vertices[face.c].x;
        positions[index + 7] = vertices[face.c].y;
        positions[index + 8] = vertices[face.c].z;

        normals[index] = face.normal.x;
        normals[index + 1] = face.normal.y;
        normals[index + 2] = face.normal.z;

        normals[index + 3] = face.normal.x;
        normals[index + 4] = face.normal.y;
        normals[index + 5] = face.normal.z;

        normals[index + 6] = face.normal.x;
        normals[index + 7] = face.normal.y;
        normals[index + 8] = face.normal.z;

        colors[index] = color.r;
        colors[index + 1] = color.g;
        colors[index + 2] = color.b;

        colors[index + 3] = color.r;
        colors[index + 4] = color.g;
        colors[index + 5] = color.b;

        colors[index + 6] = color.r;
        colors[index + 7] = color.g;
        colors[index + 8] = color.b;

        index += 9;
      }
    }
    this.bufferGeometry.addAttribute('position', new THREE.BufferAttribute(positions, 3));
    this.bufferGeometry.addAttribute('normal', new THREE.BufferAttribute(normals, 3));
    this.bufferGeometry.addAttribute('color', new THREE.BufferAttribute(colors, 3));

    this.bufferGeometry.verticesNeedUpdate = true;
    //this.bufferGeometry.computeBoundingSphere();
    //this.bufferGeometry.computeVertexNormals();

    var mesh = new THREE.Mesh(
      this.bufferGeometry,
      new THREE.MeshPhongMaterial({
        //color: 0xFFFFFF,
        //specular: 0xFFFFFF,
        shininess: 5,
        //transparent: true,
        //opacity: 0.9,
        side: THREE.DoubleSide,
        vertexColors: THREE.VertexColors
      })
    );
    this.scene.add(mesh);
  }
  updateMap() {
    // for(var i = 0; i < this.countries.length; i++) {
    //   for(var k = 0; k < this.countries[i].geometry.vertices.length; k++) {
    //     this.countries[i].geometry.vertices[k].copy(this.countries[i].geometry2D.vertices[k]);
    //     this.mix(this.countries[i].geometry.vertices[k], this.countries[i].geometry3D.vertices[k], this.interpolatePos);
    //   }
    //   //this.countries[i].geometry.computeBoundingSphere();
    //   //if(computeFaceNormals) this.countries[i].geometry.computeFaceNormals();
    // }

    var positions = this.bufferGeometry.getAttribute('position').array;
    var normals = this.bufferGeometry.getAttribute('normal').array;
    var colors = this.bufferGeometry.getAttribute('color').array;

    // var m = new THREE.Matrix4();
    // var m1 = new THREE.Matrix4();
    // var m2 = new THREE.Matrix4();
    // m1.makeRotationY(worldMap.countriesObject3D.rotation.y);
    // m2.makeScale(worldMap.countriesObject3D.scale.x, worldMap.countriesObject3D.scale.y, worldMap.countriesObject3D.scale.z);
    // m.multiplyMatrices(m1, m2);

    var color = new THREE.Color();
    var v = new THREE.Vector3();
    var index = 0;
    var i, f;
    for(i = 0; i < this.countries.length; i++) {
      var vertices = this.countries[i].geometry.vertices;
      for(var k = 0; k < vertices.length; k++) {
        vertices[k].copy(this.countries[i].geometry2D.vertices[k]);
        this.mix(vertices[k], this.countries[i].geometry3D.vertices[k], this.interpolatePos);
      }
      color.set(this.countries[i].color);
      for(f = 0; f < this.countries[i].geometry.faces.length; f++) {
        var face = this.countries[i].geometry.faces[f];

        v.copy(vertices[face.a]);

        positions[index] = v.x;
        positions[index + 1] = v.y;
        positions[index + 2] = v.z;

        v.copy(vertices[face.b]);

        positions[index + 3] = v.x;
        positions[index + 4] = v.y;
        positions[index + 5] = v.z;

        v.copy(vertices[face.c]);
        //v.applyMatrix4(m);

        positions[index + 6] = v.x;
        positions[index + 7] = v.y;
        positions[index + 8] = v.z;

        // normals
        normals[index] = face.normal.x;
        normals[index + 1] = face.normal.y;
        normals[index + 2] = face.normal.z;

        normals[index + 3] = face.normal.x;
        normals[index + 4] = face.normal.y;
        normals[index + 5] = face.normal.z;

        normals[index + 6] = face.normal.x;
        normals[index + 7] = face.normal.y;
        normals[index + 8] = face.normal.z;

        // colors
        colors[index] = color.r;
        colors[index + 1] = color.g;
        colors[index + 2] = color.b;

        colors[index + 3] = color.r;
        colors[index + 4] = color.g;
        colors[index + 5] = color.b;

        colors[index + 6] = color.r;
        colors[index + 7] = color.g;
        colors[index + 8] = color.b;
        index += 9;
      }
    }
    this.bufferGeometry.attributes.position.needsUpdate = true;
    this.bufferGeometry.attributes.normal.needsUpdate = true;
    this.bufferGeometry.attributes.color.needsUpdate = true;

    // worldMap.bufferGeometry.colorsNeedUpdate = true;
    this.bufferGeometry.computeBoundingSphere();
    // worldMap.bufferGeometry.computeVertexNormals();

    // var positions = this.bufferGeometry.getAttribute('position').array;
    // for(var i = 0; i < this.countries.length; i++) {
    //   var vertices = this.countries[i].geometry.vertices;
    //   var index = 0;
    //   for(var k = 0; k < this.countries[i].geometry.vertices.length; k++) {
    //     this.countries[i].geometry.vertices[k].copy(this.countries[i].geometry2D.vertices[k]);
    //     this.mix(this.countries[i].geometry.vertices[k], this.countries[i].geometry3D.vertices[k], this.interpolatePos);
    //     positions[3 * index] = this.countries[i].geometry.vertices[k].x;
    //     positions[3 * index + 1] = this.countries[i].geometry.vertices[k].y;
    //     positions[3 * index + 2] = this.countries[i].geometry.vertices[k].z;
    //     index++;
    //   }
    // }
    // this.bufferGeometry.attributes.position.needsUpdate = true;
  }
  createBox() {
    var base = new THREE.Geometry();
    for(var i = 0; i < this.Geo.features.length; i++) {
      var gm = this.Geo.features[i].geometry;
      var coordinates = gm.coordinates;
      if(gm.type == 'Polygon') {
        var path = [];
        for(var a = 0; a < coordinates.length; a++) {
          for(var b = 0; b < coordinates[a].length; b++) {
            var v = this.createVector3('plane', coordinates[a][b][0], coordinates[a][b][1]);
            path.push(v);
          }
        }
        var shape = new THREE.Shape(path);
        var geometry = new THREE.ExtrudeGeometry(shape, {
          bevelEnabled: false,
          amount: 5
        });
        base.merge(geometry);
      } else if(gm.type == 'MultiPolygon') {
        for(var a = 0; a < coordinates.length; a++) {
          var path = [];
          for(var b = 0; b < coordinates[a].length; b++) {
            for(var c = 0; c < coordinates[a][b].length; c++) {
              var v = this.createVector3('plane', coordinates[a][b][c][0], coordinates[a][b][c][1]);
              path.push(v);
            }
          }
          var shape = new THREE.Shape(path);
          var geometry = new THREE.ExtrudeGeometry(shape, {
            bevelEnabled: false,
            steps: 1,
            amount: 5
          });
          base.merge(geometry);
        }
      }
    }
    var mesh = new THREE.Mesh(base, new THREE.MeshBasicMaterial({color: 0xff0000}));
    this.scene.add(mesh);
    console.log(this.scene);
  }
  createGlobal() {
    this.countrys = {
      geometry: new THREE.Geometry(),
      geometry2D: new THREE.Geometry(),
      geometry3D: new THREE.Geometry()
    };
    var count = 0;
    for(var i = 0; i < this.Geo.features.length; i++) {
      var gm = this.Geo.features[i].geometry;
      var coordinates = gm.coordinates;
      if(gm.type == 'Polygon') {
        for(var a = 0; a < coordinates.length; a++) {
          for(var b = 0; b < coordinates[a].length; b++) {
            this.countrys.geometry.vertices.push(this.createVector3('plane', coordinates[a][b][0], coordinates[a][b][1]));
            this.countrys.geometry2D.vertices.push(this.createVector3('plane', coordinates[a][b][0], coordinates[a][b][1]));
            this.countrys.geometry3D.vertices.push(this.createVector3('sphere', coordinates[a][b][0], coordinates[a][b][1]));
            count++;
          }
        }
      } else if(gm.type == 'MultiPolygon') {
        for(var a = 0; a < coordinates.length; a++) {
          for(var b = 0; b < coordinates[a].length; b++) {
            for(var c = 0; c < coordinates[a][b].length; c++) {
              this.countrys.geometry.vertices.push(this.createVector3('plane', coordinates[a][b][c][0], coordinates[a][b][c][1]));
              this.countrys.geometry2D.vertices.push(this.createVector3('plane', coordinates[a][b][c][0], coordinates[a][b][c][1]));
              this.countrys.geometry3D.vertices.push(this.createVector3('sphere', coordinates[a][b][c][0], coordinates[a][b][c][1]));
              count++;
            }
          }
        }
      }
    }
    this.bufferGeometry = new THREE.BufferGeometry();
    var positions = new Float32Array(count * 3);
    this.bufferGeometry.addAttribute('position', new THREE.BufferAttribute(positions, 3));
    var mesh = new THREE.Line(this.bufferGeometry, new THREE.MeshBasicMaterial({
      color: 'blue',
      side: THREE.DoubleSide
    }));
    this.scene.add(mesh);
  }
  updateGlobal() {
    var positions = this.bufferGeometry.getAttribute('position').array;
    var index = 0;
    for(var k = 0; k < this.countrys.geometry.vertices.length; k++) {
      this.countrys.geometry.vertices[k].copy(this.countrys.geometry2D.vertices[k]);
      this.mix(this.countrys.geometry.vertices[k], this.countrys.geometry3D.vertices[k], this.interpolatePos);
      positions[3 * index] = this.countrys.geometry.vertices[k].x;
      positions[3 * index + 1] = this.countrys.geometry.vertices[k].y;
      positions[3 * index + 2] = this.countrys.geometry.vertices[k].z;
      index++;
    }
    this.bufferGeometry.attributes.position.needsUpdate = true;
  }
  createLight() {
    this.scene.add(new THREE.AmbientLight(0xffffff));
  }
  mix(v1, v2, factor) {
    v1.x = v1.x + (v2.x - v1.x) * factor;
    v1.y = v1.y + (v2.y - v1.y) * factor;
    v1.z = v1.z + (v2.z - v1.z) * factor;
  }
  createVector3(type, lon, lat) {
    if(type == 'sphere') {
      return new THREE.Vector3(
        -this.radius * Math.sin((90 - lat) * Math.PI / 180) * Math.cos((lon - 180) * Math.PI / 180),
        this.radius * Math.cos((90 - lat) * Math.PI / 180),
        this.radius * Math.sin((90 - lat) * Math.PI / 180) * Math.sin((lon - 180) * Math.PI / 180)
      );
    } else if(type == 'plane') {
      return new THREE.Vector3(
        (lon / 180) * this.radius * 2,
        (lat / 180) * this.radius * 2
      );
    }
  }
  render() {
    requestAnimationFrame(this.animate);
    this.controls.update();
    this.renderer.clear();
    this.renderer.render(this.scene, this.camera);
    if(this.geometryNeedsUpdate) {
      //this.updateGlobal();
      this.updateMap();
    }
  }
  load(data, type) {
    if(!this.isAnimate) {
      this.animate();
      this.isAnimate = true;
    }
  }
};